$orange: #f05f40;
$error: #ba0000;

body {
  margin: 0;
  color: #222;
}

h1, h2, h3, h4, h5 {
  font-family: "Open Sans","Helvetica Neue",Arial,sans-serif;
}

#home {
  background: url('/img/pdx_bg_dark_1.jpg') 50% 0 repeat fixed;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  position: relative;

  h1.home-head {
    font-size: 5.25em;
    background-color: rgba(0, 0, 0, 0.9);
    line-height: 1.425em;
    display: inline;
  }

  h1.home-head span {
    padding: 0.2em;
  }

  .head-container {
    margin-top: 15%;
  }
}

#about {
  font-size: 1.125em;
  background: $orange;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  position: relative;

  #bio-image {
    background: url('/img/justin-headshot-250.jpg') no-repeat;
    border: 6px solid rgba(255, 255, 255, 1);
  }

  #skillset {
    background: #fff;
  }

  #bio {
    padding: 2em 0 4em 0;
  }

  #bio-text {
    padding-left: 2em;
    line-height: 1.725em;
  }

  #skillset {
    padding: 2em 0;
  }

  ul.skills {
    padding-left: 20px;
    width: 200px;
    display:-moz-inline-stack;
    display:inline-block;
    zoom:1;
    *display:inline;
    margin: 0 auto;
    text-align: left;
  }

  a:link {
    color: #222;
    text-decoration: none;
  }

  a:active {
    color: #000;
    text-decoration: underline;
  }

  a:hover {
    color: #fff;
    text-decoration: underline;
  }

  a:visited {
    color: #000;
    text-decoration: none;
  }
}

#portfolio {
  background: url('/img/pdx_bg_dark_1.jpg') 50% 0 repeat fixed;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  position: relative;
}

#contact {
  background: url('/img/pdx_bg_dark_1.jpg') 50% 0 repeat fixed;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  position: relative;

  div.contact-errors {
    display: none;
  }
}

ul.skills {
  list-style-type: none;
}

#portfolio, #contact {
  padding-top: 24em;
}

.white-bg {
  background: #fff;
}

.white {
  color: #fff;
}

.orange {
  color: $orange;
}

.buffer-top {
  padding-top: 50px !important;
}

nav.navbar-inverse {
  background-image: none;
  background-color: rgba(0, 0, 0, 0.75);
  border: 0 none;
}

.circular {
  width: 250px;
  height: 250px;
  border-radius: 125px;
  -webkit-border-radius: 125px;
  -moz-border-radius: 125px;
}

ul.skills li:before, ul.project-skill li:before {
  font-family: 'FontAwesome';
  content: "\f054";
  margin:0 5px 0 -15px;
  color: $orange;
}

.short-hr {
  max-width: 50px;
  border-width: 3px;
}

.orange-hr {
  border-color: $orange;
}

.indent {
  text-indent: 1.25em;
}

/*Scroll Down Animation*/
a#scroll-down {
  @-moz-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      -moz-transform: translateY(0);
      transform: translateY(0);
    }
    40% {
      -moz-transform: translateY(-30px);
      transform: translateY(-30px);
    }
    60% {
      -moz-transform: translateY(-15px);
      transform: translateY(-15px);
    }
  }
  @-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
      transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
      transform: translateY(-15px);
    }
  }
  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      -moz-transform: translateY(0);
      -ms-transform: translateY(0);
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
    40% {
      -moz-transform: translateY(-30px);
      -ms-transform: translateY(-30px);
      -webkit-transform: translateY(-30px);
      transform: translateY(-30px);
    }
    60% {
      -moz-transform: translateY(-15px);
      -ms-transform: translateY(-15px);
      -webkit-transform: translateY(-15px);
      transform: translateY(-15px);
    }
  }

  .arrow {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: -20px;
    width: 40px;
    height: 40px;
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
  }

  .bounce {
    -moz-animation: bounce 2s infinite;
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
  }
}